.emailContainer {
    display: flex;
    align-items: center;
    width: auto;
    height: 63px;
    background: #FFFFFF;
    border: 1px solid rgba(161, 174, 183, 0.1);
    box-sizing: border-box;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.04);
    border-radius: 100px;
}

.inputField {
    margin-left: 25px;
    border: none;
    font-size: 17px;
    font-family: Circular Std;
    font-style: normal;
    font-weight: normal;
    line-height: 22px;
    color: #334E68;
}

/* can the button be right aligned? */

.submitEmailButton {
    position: relative;
    margin-left: auto;
    margin-right: 10px;

    display: flex;
    align-items: center;
    justify-content: center;

    width: 150px;
    height: 43px;
    background: #F86A6A;
    box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.05);
    border-radius: 100px;
    font-family: Circular Std;
    font-style: normal;
    font-weight: bold;
    font-size: 15px;
    line-height: 19px;
    text-align: center;
    letter-spacing: -0.3px;
    color: #FFFFFF;
    outline: none;
    border: none;
}

.confirmationText {
    width: 100%;
    margin-bottom: 10px;
    font-family: Circular Std;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 19px;
}

input, textarea, select, a { outline: none; }